.MainFistAxess {
  background-color: #f5f3ec;
  width: 100%;
  height: 100%;
  position: relative;
}

.MainFistAxes {
  width: 100%;
  height: 100%;
  transition-property: opacity;
  transition-duration: 2s;
  transition-timing-function: ease-in;
  position: absolute;
}

