  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.LogOuts__content {
  background: linear-gradient(to bottom, rgb(233, 222, 201), rgb(170, 170, 170));
  margin: 20% auto;
  padding: 2%;
  border: 3px solid rgb(90, 88, 89);
  width: 31%;
  border-radius: 30px;
}

.LogOuts__content__pic {
  border-radius: 100px;
  border: 5px solid #caaa89;
  width: 100%;
  height: 100%;
}

.scrollYears {
  display: flex;
  justify-content: center;
  font-size: 5vh;
  font-weight: 800;
}

.scrollYear_on {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  opacity: 1;
  transition-property: opacity left;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.09, 0.51, 0.5, 0.94);
}

.scrollYear_in {
  position: absolute;
  left: 62%;
  transform: translate(-50%);
  opacity: 0;
  transition-property: opacity left;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.09, 0.51, 0.5, 0.94);
}

.scrollYear_out {
  position: absolute;
  left: 38%;
  transform: translate(-50%);
  opacity: 0;
  transition-property: opacity left;
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0.09, 0.51, 0.5, 0.94);
}
